<template>
    <home-container >
        <div class=' d-flex flex-column justify-content-end' style='min-height:90vh'>
            <div>

                <h2 class='mb-5'>Welcome Jacky</h2>
                <ion-label  position="stacked">Password</ion-label>
                <ion-item  class="rounded border" lines="none">
                    <ion-input :type="showPassword ? 'text':'password'" v-model="password"></ion-input>
                    <ion-button @click.prevent="showPassword = !showPassword" color='dark' fill='clear' slot='end'>
                        <ion-icon v-if="!showPassword" slot="icon-only" :icon='eyeOffOutline'></ion-icon>
                        <ion-icon v-else slot="icon-only" :icon='eyeOutline'></ion-icon>
                    </ion-button>
                </ion-item>
                <div class='d-flex justify-content-end ion-margin-bottom'>
                    <ion-button fill='clear' size='small'>Forgot Password?</ion-button>
                </div>
                <ion-button strong :disabled="!password || password.length < 4" @click="handleLogin" class="mt-4 " expand="block">Login</ion-button>
                <ion-button strong @click="router.push('/sign-in')" fill='clear' expand="block">Switch</ion-button>
            </div>
        </div>
    </home-container>
    
</template>
<script>
import { defineComponent } from "vue";
import {

} from "@ionic/vue";
import {eyeOffOutline,eyeOutline } from "ionicons/icons";
import { useRouter } from "vue-router";
import { loadingController } from "@ionic/vue";

    export default defineComponent({
        components: { },
        data() {
            return {
                password:null,
                showPassword:false,
            }
        },
        setup() {
            const router = useRouter();
            return {
                router,eyeOffOutline,eyeOutline
            };
        },
        methods:{
            async handleLogin(){
                let loading = await loadingController.create({
                    message: "Please wait...",
                });
                loading.present();
                setTimeout(() => {
                    this.router.push('/home');
                    loading.dismiss();
                }, 1000);
            },
        }
});

</script>
